import { Fragment, h } from "preact";
import styled from "styled-components";

type Props = {
	choices: { value: string; label: string }[];
	dropdownName: string;
	handleChange: any;
	selected: string;
	className?: string;
};

const StyledSelect = styled.select`
	max-width: 40rem;
	width: 100%;
	height: 5.2rem;
	font-size: 1.6rem;
	border-radius: 0.4rem;
	padding: 0 1.6rem;

	&::marker {
		margin-right: 1.6rem;
	}

	& option {
		/* display: none */
		font-size: 1.6rem;

		& p {
			padding: 1.6rem 0;
		}
	}

	&:after {
		border-color: pink;
	}
`;

// const StyledOption = styled.div`
// 	background: pink;
// `;

const Dropdown = ({
	selected,
	handleChange,
	choices,
	dropdownName,
	className,
}: Props) => {
	return (
		<StyledSelect
			className={className}
			name={dropdownName}
			id={dropdownName}
			onChange={handleChange}
			value={selected}
		>
			{choices.map((choice, key) => {
				return (
					<Fragment>
						<option
							key={key}
							value={choice.value}
							// selected={selected === choice.value ? true : false}
						>
							<p> {choice.label}</p>
						</option>
						{/* <StyledOption key={key + "_styledOption"}>
							<p> {choice.label}</p>
						</StyledOption> */}
					</Fragment>
				);
			})}
		</StyledSelect>
	);
};

export default Dropdown;
