import { Product } from "../../components/Product";
import { products } from "../../assets/data/products";
import { ShopFront } from "../../components/ShopFront";
import { h } from "preact";
import { categoryChoices } from "../../components/helpers/categoryChoices";

const Shop = ({ productSlug }) => {
	const product = products.find((product) => product.slug === productSlug);
	const category = categoryChoices.indexOf(productSlug) ? productSlug : false;
	return productSlug && product ? (
		<Product product={product} />
	) : (
		<ShopFront category={category} />
	);
};

export default Shop;
