import { h } from "preact";
import styled from "styled-components";

type Props = {
	children: string;
	handleClick: () => void;
	variant?: "primary" | "secondary";
	className?: string;
	[k: string]: any;
};

const StyledButton = styled.button<{ variant: "primary" | "secondary" }>`
	padding: 0.8rem 1.6rem;
	border: 1px solid #efefef;
	border-radius: 0.4rem;
	background: #1b1b1b;
	outline: none;
	color: #efefef;
	text-transform: uppercase;
	font-weight: 600;
	min-width: 10rem;
	height: 5.2rem;
	cursor: pointer;
`;

export const Button = ({
	children,
	handleClick,
	variant = "primary",
	className = "",
	...props
}: Props) => {
	return (
		<StyledButton
			onClick={handleClick}
			variant={variant}
			className={className}
			// prop spreading very important for snip cart data
			{...props}
		>
			{children}
		</StyledButton>
	);
};
