import { h } from "preact";
import { ProductType } from "../assets/types/ProductsTypes";
import styled from "styled-components";
import { displayAsCurrency } from "./helpers/displayAsCurrency";
import { Image } from "./helpers/Image";
import { Input } from "./helpers/Input";
import { useCallback, useState } from "preact/hooks";
import { Button } from "./helpers/Button";
import Dropdown from "./helpers/Dropdown";

const StyledWrapper = styled.div`
	max-width: 80rem;
	margin: 2.4rem auto;
	padding: 1.6rem;
`;

type Props = {
	product: ProductType;
};

const StyledHeroImage = styled.img`
	width: 100%;
	max-height: 40rem;
	margin: 1.6rem auto;
	object-fit: contain;
`;

const StyledCurrency = styled.p``;

const StyledDescription = styled.div`
	margin: 1.6rem 0;
`;

const StyledImagesWrapper = styled.div`
	display: flex;
	flex-direction: column;
`;

const StyledGallery = styled.div`
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	grid-gap: 0.8rem;
`;

const StyledContent = styled.div`
	margin: 1.6rem 0;
	display: flex;
	flex-direction: column;
`;

const StyledUl = styled.ul`
	margin-left: 1.6rem;
`;

const StyledImage = styled(Image)`
	opacity: 0.5;
	transition: opacity 0.5s ease-in-out;
	cursor: pointer;

	&:hover {
		opacity: 1;
		transition: opacity 0.5s ease-in-out;
	}
`;

const StyledAddItemWrapper = styled.div`
	display: flex;
	justify-content: flex-end;
	grid-gap: 1.6rem;
	margin: 2.4rem 0;
`;

const StyledProductDetails = styled.div``;

export const Product = ({ product }: Props) => {
	const [heroImage, set_heroImage] = useState(product?.gallery[0]);
	const descriptionArray = product?.description.split("<br/>");

	const handleGalleryImageClick = useCallback((image) => {
		set_heroImage(image);
	}, []);

	const [inputValue, set_inputValue] = useState<number>(1);

	const handleInputChange = useCallback((input) => {
		console.log(input);
		set_inputValue(input.target.value);
	}, []);

	console.log({ inputValue });

	const colourOptions = product?.colours.join("|");

	const choices =
		product?.colours &&
		product?.colours.map((colour) => {
			return { value: colour, label: colour };
		});

	const [colour, set_colour] = useState<string>(choices?.[0].value || "");
	const handleColourChange = useCallback((input) => {
		const colour = (input.target as HTMLTextAreaElement).value;
		set_colour(colour);
	}, []);

	return (
		<StyledWrapper>
			<StyledImagesWrapper>
				<StyledHeroImage src={`/assets/images/${heroImage.src}`} />
				<StyledGallery>
					{product?.gallery.map((image) => {
						return (
							<StyledImage
								src={image?.src}
								alt={image?.alt}
								height={10}
								width={10}
								handleClick={() => handleGalleryImageClick(image)}
							/>
						);
					})}
				</StyledGallery>
			</StyledImagesWrapper>
			<StyledAddItemWrapper>
				{choices && (
					<Dropdown
						choices={choices}
						dropdownName="colours"
						handleChange={handleColourChange}
						selected={colour}
					/>
				)}
				<Input
					type="number"
					value={inputValue}
					handleChange={handleInputChange}
				/>
				<Button
					handleClick={() => {}}
					className="snipcart-add-item"
					data-item-id={product.slug}
					data-item-image={`/assets/images/${product.gallery[0].src}`}
					data-item-name={product.title}
					data-item-description="test"
					data-item-url={`/shop/${product.slug}`}
					data-item-price={product.price}
					data-item-has-taxes-included="true"
					data-item-custom1-name={product?.colours ? "Colours" : ""}
					data-item-custom1-options={product?.colours ? colourOptions : ""}
					// data-item-custom1-value={}
					data-item-quantity={inputValue}
				>
					Add to cart
				</Button>
			</StyledAddItemWrapper>
			<StyledContent>
				<span>
					<h1>{product?.title}</h1>
					<StyledCurrency>
						{displayAsCurrency(product?.price)} incl GST
					</StyledCurrency>
					<StyledDescription>
						{descriptionArray.map((chunk) => {
							return (
								<span>
									{chunk}
									<br />
									<br />
								</span>
							);
						})}
					</StyledDescription>
				</span>
				<StyledProductDetails>
					<strong>Dimensions: </strong>
					<br />
					<br />
					{product?.dimensions}
					<br />
					<br />
					<strong>Available colours: </strong>
					<br />
					<br />
					<StyledUl>
						{product?.colours.map((colour) => (
							<li>{colour}</li>
						))}
					</StyledUl>
				</StyledProductDetails>
			</StyledContent>
		</StyledWrapper>
	);
};
