import { h } from "preact";
import { Link } from "preact-router";
import styled from "styled-components";
import { ProductType } from "../assets/types/ProductsTypes";
import { displayAsCurrency } from "./helpers/displayAsCurrency";
import { DivImage } from "./helpers/DivImage";
import { Image } from "./helpers/Image";

type Props = {
	product: ProductType;
	handleClick: () => void;
	href: string;
};

const StyledWrapper = styled.div`
	max-width: 20rem;
	aspect-ratio: 1;
	/* margin: 0 0.8rem 0.8rem 0; */
`;

const StyledImageWrapper = styled.div`
	display: flex;
	align-items: center;
	overflow: hidden;
	width: 20rem;
	aspect-ratio: 1;
`;

const StyledTitle = styled.p`
	font-weight: 600;
`;

const StyledLink = styled(Link)`
	--black: #1b1b1b;

	text-decoration: none;
	color: var(--black);
`;

const StyledCurrency = styled.p``;
export const ProductTile = ({ product, handleClick, href }: Props) => {
	return (
		<StyledLink onClick={handleClick} href={href}>
			<StyledWrapper>
				<DivImage
					width={20}
					src={product?.gallery?.[0]?.src}
					alt={product?.gallery?.[0]?.alt}
				/>
				<StyledTitle>{product?.title}</StyledTitle>
				<StyledCurrency>
					{displayAsCurrency(product?.price)} incl GST
				</StyledCurrency>
			</StyledWrapper>
		</StyledLink>
	);
};
