import { h } from "preact";
import styled from "styled-components";

const StyledInput = styled.input`
	width: 5.2rem;
	text-align: center;
	&::-webkit-outer-spin-button,
	&::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	&[type="number"] {
		--moz-appearance: textfield;
	}
`;

type Props = {
	type: "number" | "tel" | "text" | "password";
	className?: string;
	value: string | number;
	handleChange: (input: InputEvent) => void;
};

export const Input = ({ type, className = "", value, handleChange }: Props) => {
	return (
		<StyledInput
			type={type}
			className={className}
			value={value}
			onChange={handleChange}
		/>
	);
};
