import { Fragment, h } from "preact";
import { useEffect, useState } from "preact/hooks";
import styled from "styled-components";
import { products } from "../assets/data/products";
import { ProductType } from "../assets/types/ProductsTypes";
import {
	CategoryValue,
	categoryChoices,
	Category,
	CategoryLabels,
} from "./helpers/categoryChoices";
import Dropdown from "./helpers/Dropdown";
import useWindowState from "./helpers/useWindowState";
import { ProductTile } from "./ProductTile";

type Props = {
	category: CategoryValue;
};

const StyledWrapper = styled.div`
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 2.4rem 1.6rem;
	grid-gap: 1.6rem;
`;

const StyledTilesWrapper = styled.div`
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 2.4rem 1.6rem;
	grid-gap: 1.6rem;
`;

export const ShopFront = ({ category }: Props) => {
	const handleProductClick = () => {};

	const [selected, set_selected] = useState(
		category ?? categoryChoices[0].value
	);

	useEffect(() => {
		if (category) {
			set_selected(category);
		}
	}, []);

	const handleChange = (e) => {
		const value = e.target.value;
		set_selected(value);
		window.history.replaceState(null, "", "/shop");
	};

	return (
		<StyledWrapper>
			{categoryChoices && (
				<Dropdown
					choices={categoryChoices}
					dropdownName="category"
					handleChange={handleChange}
					selected={selected}
				/>
			)}
			<StyledTilesWrapper>
				{selected === "all"
					? products.map((product: ProductType, key) => {
							return (
								<ProductTile
									key={key}
									product={product}
									handleClick={handleProductClick}
									href={"/shop/" + product?.slug}
								/>
							);
					  })
					: products.map((product, key) => {
							console.log(product.productCategory);
							return product.productCategory.indexOf(selected) !== -1 ? (
								<ProductTile
									key={key}
									product={product}
									handleClick={handleProductClick}
									href={"/shop/" + product?.slug}
								/>
							) : (
								<Fragment />
							);
					  })}
			</StyledTilesWrapper>
		</StyledWrapper>
	);
};
